import Todo from "./todo";

const todoArr = [];
let TodoCounter = 0;

function addToDo() {
    console.log("working");
    const titleEl = document.getElementById("title");
    const closeEl = document.getElementsByClassName("close");
    let close = closeEl[0];
    let description = document.getElementById("description").value;
    let dueDate = document.getElementById("dueDate").value;
    let priority = document.getElementById("priority").value;
    let project = document.getElementById("project").value;
    let title = titleEl.value;
    let valid = validTitle(title);
    if (valid) {
        let todoObj = new Todo(title, description, dueDate, priority, project, TodoCounter);
        todoArr.push(todoObj);
        titleEl.style.backgroundColor = "white";
        close.click();
        TodoCounter++;
        console.log(todoArr)
    } else {
        titleEl.style.backgroundColor = "pink";
    }
}

function validTitle(title) {
    if (title === "") {
        return false
    };
    return true;
}

export default addToDo;